d: false,\n              paid_node: storage.paid_node,\n              amount: storage.amount,\n              url: buildURL(createAPI(`files/${storage.file}`), {w: 250, h: 250, token: this.token})\n            };\n          } else { // 已付费\n            return {\n              paid: true,\n              paid_node: storage.paid_node,\n              amount: storage. amount,\n              url: buildURL(createAPI(`files/${storage.file}`), {w: 300, h: 300, token: this.token})\n            };\n          }\n        });\n      }\n    },\n    methods: {\n      // 展示走马灯\n      showSwiper (index) {\n        let images = [];\n        this.imageObject.forEach((storage) => {\n          if( storage.url ) storage.url = (storage.url.replace(/\\?.*/, ''));\n          images.push(storage);\n        });\n        this.$store.dispatch(IMGSWIPER, cb => {\n          cb({\n            list:images,\n            value: index,\n            show: true,\n            source: this.feed,\n            sourceType: 'feed'\n          });\n        });\n      },\n\n      //展示付费\n      showPayDialog (image, index) {\n        this.$Modal.confirm({\n          title: '付费支付',\n          content: `<p>需要支付 ${showAmount(image.amount)}${this.goldName} </p>`,\n          okText: '确认支付',\n          loading: true,\n          onOk: () => {\n            addAccessToken().post(createAPI(`purchases/${image.paid_node}`), {\n              validateStatus: status => status === 201                \n            })\n            .then(() => {\n              this.$Modal.remove();\n              this.$Message.success('支付成功');\n              this.storages[index].paid = true;\n              setTimeout(() => {\n                this.showSwiper(index);\n              }, 800);\n              this.$store.getters[FEEDSLIST][this.feed].images[index].paid = true;\n            })\n            .catch( ({ response: { data, status } = {} }) => {\n              this.$Modal.remove();\n              this.$Message.error(PlusMessageBundle(data).getMessage());\n            })\n          }\n        });\n      }\n    },\n    created () {\n      const { token = ''} = this.$storeLocal.get('UserLoginInfo') || {};\n      this.token = token;\n    },\n  }\n\n  export default feedImages;\n</script>\n<style scoped >\n  .ivu-icon-help-circled {\n    color: #f90!important;\n    font-size: 36px!important;\n  }\n</style>\n<style lang=\"less\" module>\n  .FeedImages {\n    width: 100%;\n    img {\n      width: 100%;\n      object-fit: cover;\n      display: flex;\n    }\n    .imagesRow {\n      margin-bottom: 2px!important;\n    }\n  }\n  .imgParent {\n    overflow: hidden;\n    &:before {\n      content: \"\";\n      display: block;\n      padding-top: 100%;\n    }\n  }\n  .showImgBox {\n    position: relative;\n    overflow: hidden;\n    padding: 0 2px;\n    left: -1px;\n    margin-bottom: 2px;\n    .perFeedImg {\n      position: absolute;\n      top: 0;\n      left: 2px;\n      object-fit: cover;\n      height: 100%;\n      width: 100%;\n    }\n    &:before {\n      content: \"\";\n      display: block;\n      padding-top: 100%;\n    }\n  }\n</style>\n"],"sourceRoot":""}]);

